.App-logo {
  height: 50px;
  border-radius: 100px;
}
.App-logo-link {
  margin: 0% 5% 0% 2%;
}
.App-header {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  padding: 1%;
  height: 12vh;
  background-color: #ddd;
}

.App-title {
  font-size: 2.7em;
  color: #422;
}

@media screen and (max-width: 600px) {
  .App-logo {
    height: 25px;
  }
  .App-title {
    font-size: 1.5em;
  }
}



