.exp-container {
	-ms-flex: 1 1;
	    flex: 1 1;
	max-width: 33vw;
	background-color: #1ba4e3;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: start;
	    align-items: flex-start;
}
.exp-title {
	-ms-flex-item-align: center;
	    align-self: center;
}
.exp-item-container {
	margin-left: 20px;
}

@media screen and (max-width: 600px) {
	.exp-title {
		font-size: 1em;
	}
	.exp-item-container {
		margin-left: 1px;
	}
}



