.win-container {
	-ms-flex: 3 1;
	    flex: 3 1;
	background-color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}
.win-title {
	text-align: center;
}
.win-subtitle {
	margin-left: 15px;
	-ms-flex-item-align: start;
	    align-self: flex-start;
}
.win-items {
	display: block;
	width: 90%;
	font-family: 'Alegreya Sans';
}
.back-button {
	cursor: pointer;
}
.back-button:hover {
	font-weight: bolder;
}
@media screen and (max-width: 600px) {
	.win-title {
		font-size: 1em;
	}
	.win-subtitle {
		font-size: 0.7em;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
	.win-container {
		max-width: 55vw;
	}
}



