.win-item-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-right: 10%;
	width: 95%;
	height: 35px;
	text-align: left;
	-ms-flex-align: center;
	    align-items: center;
	color: #444;
}
.win-item-name {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}
.hoverable:hover {
	cursor: pointer;
	background-color: #ddd;
}

@media screen and (max-width: 600px) {
	.win-item-title {
		font-size: 0.9em;
	}
	.win-item-subtitle {
		font-size: 0.9em;
	}
}



